<template>
  <div class="about">
    <div @click="getAll">获取歌曲列表</div>
    <div v-for="(item,index) in songList" :key="index">
      <router-link :to="{ name:'music', params: { id: item.id}}">{{item.name}}</router-link>
      <!-- <a :href="'https://api.bzqll.com/music/tencent/url?key=579621905&id='+item.id">{{item.name}}</a> -->
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      songList: []
    };
  },
  methods: {
    getAll() {
      axios
        .get("https://api.bzqll.com/music/tencent/hotSongList?key=579621905")
        .then(response => {
          this.songList = response.data.data;
        })
        .catch(error => {});
    }
  }
};
</script>
